@import "../css/variables";
@import "../css/mixins";

.research {
  .research-item {
    display: flex;
    flex-direction: row;
    padding: $spacer / 2 0;

    &:first-child {
      padding-top: 0;
    }

    @media (min-width: $md) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .research-image {
    min-width: 10%;
    max-width: 10%;
    margin-right: $spacer * 1.25;
    margin-top: 5px;
    margin-bottom: $spacer;
    display: none;

    img {
      border: 1px solid #eee;
    }

    @media (min-width: $md) {
      display: block;
    }
  }

  .csl-bib-body {
    padding-bottom: $spacer;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;

    .bib-links {
      padding-top: $spacer / 2;
      display: block;
    }
  }
}
