html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 1.7;
  color: #495057; }

h1, h2, h3, h4, h5 {
  margin: 2rem 0 2rem 0;
  font-weight: 400; }

h1 {
  font-size: 3.5rem; }

h2 {
  font-size: 2.8rem;
  margin-bottom: 4rem;
  padding-top: 7rem;
  text-align: center; }

h3 {
  font-size: 2.45rem; }

h4 {
  font-size: 2.1rem; }

h5 {
  font-size: 1.75rem; }

h6 {
  font-size: 1.4rem;
  font-weight: 400; }

.container {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto; }
  @media (min-width: 576px) {
    .container {
      max-width: 540px; } }
  @media (min-width: 768px) {
    .container {
      max-width: 720px; } }
  @media (min-width: 952px) {
    .container {
      max-width: 920px; } }
  @media (min-width: 1160px) {
    .container {
      max-width: 1100px; } }

.img-fluid {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #1d71b8; }
  a:hover {
    text-decoration: underline;
    color: #16568c; }

.btn {
  display: inline-block;
  border-radius: 0.75rem;
  padding: 0.5rem 1.5rem;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out;
  background-color: #6c757d;
  color: #fff; }
  .btn:hover {
    background-color: #545b62;
    text-decoration: none;
    color: #fff; }
  .btn .feather {
    margin-bottom: 4px; }
  .btn .simple-icon {
    fill: currentColor;
    width: 24px;
    margin-bottom: 4px; }

img {
  vertical-align: middle; }

svg {
  overflow: hidden;
  vertical-align: middle; }

.btn-primary {
  background-color: #1d71b8;
  color: #fff; }
  .btn-primary:hover {
    background-color: #16568c; }

.container-smaller {
  max-width: 900px; }

.main-content p {
  text-align: justify; }

.page-content {
  margin-top: 7rem; }

code {
  font-size: .93em;
  color: #cc6b6b; }

code, pre {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace; }

.hljs, pre {
  background: #495057;
  color: #fff; }

pre {
  border-radius: 0.75rem; }
  pre code {
    margin: 0.5rem 1rem;
    display: block; }
